import Observer from './index.js'
export default {
  name: 'observer',
  data() {
    return {
      observer: {}
    }
  },
  methods: {
    onSubscribeClick(id, text) {
      this.observer.subscribe({
        id,
        name: 'guan',
        fn: () => {
          console.log(text)
        }
      })
      console.log('添加一条订阅' + text)
    },
    onTriggerClick() {
      this.observer.trigger('guan')
      console.log('开始上课了')
    },
    onTakeOffClick(id) {
      this.observer.takeOff({ name: 'guan', id })
      console.log(id + '取消关注')
    }
  },
  created() {
    this.observer = new Observer()
  }
}



